@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color: #8BC6EC;
  background-image: linear-gradient(135deg, #2A2A72 0%, #348CBF 100%);  
  height: 100vh;
  width: 100vw;
}

.App-logo {
  height: 50%;
  pointer-events: none;
}
.logoholder{
  min-width: 45%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.headname{
  color: white;
  padding-left: 5px;
  font-weight: 600;
  font-family: 'Roboto';
  letter-spacing: 0.3rem;
  vertical-align: bottom;
  line-height: 6vh;
}
.headnameholder{
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
 
}
.App-header {
  height: 10%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  font-size: 2.2vh;
}
.contentArea{
  height: 90%;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
}
.content{
  height: 90%;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.welcome{
  color: white;
  font-family: 'Roboto';
  font-weight: 700;
  font-size: 3vh;
}
.playbadge{
  width: 70%;
}
.badgeholder{
  width: 100%;
  height: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 480px) {
  .welcome {
    font-size: 5vh;
  }
  .playbadge{
    width: 30%;
  }
}
